import React, { useEffect, useRef, useState } from 'react'
import Paint from '..'
// @ts-ignore
import exampleImg from './example.jpg'

export function TestComponent() {
  const ref = useRef<Paint>()
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const cursorRef = useRef<HTMLDivElement>(null)
  const [toolname, setToolname] = useState('')
  const [lineW, setLineW] = useState<number>(15)
  const [resultUrl, setResultUrl] = useState('')
  useEffect(() => {
    const p = new Paint(canvasRef.current!, cursorRef.current!)
    p.loadBottomImage(exampleImg)
    p.setLineWidth(15)
    setLineW(15)
    p.selectBrush()
    setToolname('')
    ref.current = p
  }, [])
  return (
    <div>
      <h3></h3>
      <div
        style={{
          position: 'relative',
        }}
      >
        <canvas
          width={500}
          height={500}
          style={{ width: '500px', height: '500px' }}
          ref={canvasRef}
        ></canvas>
        <div
          style={{
            position: 'absolute',
          }}
          ref={cursorRef}
        ></div>
      </div>

      <p></p>
      <br />
      <p>{toolname}</p>
      <div
        style={{
          display: 'flex',
          flexDirection: 'column',
          gap: '8px',
          padding: '8px',
        }}
      >
        <p></p>
        <button
          onClick={() => {
            ref.current?.undo()
          }}
        >
          
        </button>
        <button
          onClick={() => {
            ref.current?.redo()
          }}
        >
          
        </button>
        <button
          onClick={() => {
            ref.current?.selectBrush()
            setToolname('')
          }}
        >
          
        </button>
        <button
          onClick={() => {
            ref.current?.selectErase()
            setToolname('')
          }}
        >
          
        </button>
        <button
          onClick={() => {
            ref.current?.selectSelect()
            setToolname('')
          }}
        >
          
        </button>
        <div style={{ fontSize: '14px' }}>
          
          <input
            type="range"
            value={lineW}
            min={1}
            max={30}
            onChange={e => {
              ref.current?.setLineWidth(Number(e.target.value))
              setLineW(Number(e.target.value))
            }}
          />
        </div>

        <p></p>
        <button
          onClick={async () => {
            const blob = (await ref.current?.saveMask()) as Blob
            if (!blob) return
            await getBase64AsPromise(blob).then(url => {
              setResultUrl(url)
            })
          }}
        >
          
        </button>
      </div>

      {resultUrl && (
        <div>
          <h3></h3>
          <img src={resultUrl} alt="" />
        </div>
      )}
    </div>
  )
}

function getBase64AsPromise(img: File | Blob | undefined): Promise<string> {
  return new Promise((resolve, reject) => {
    if (img) {
      const reader = new FileReader()
      reader.addEventListener('load', () => {
        if (reader.result && typeof reader.result === 'string') {
          resolve(reader.result)
        }
      })
      reader.readAsDataURL(img)
    }
  })
}
